
import {Component, Prop, Vue, Emit, Watch, PropSync} from "vue-property-decorator";
import Service from '../../https/disk/list'
import {Form} from "element-ui";
import th from "element-ui/src/locale/lang/th";
@Component({})

export default class Iops extends Vue{
  @PropSync('visible') visibleSync!:Boolean;
  @Prop(String) title:string
  @Prop({default:()=>[]}) mount_id!:any
  @Prop(String) oper_type!:string;
  private second_visible:boolean=false
  private min_iops:number = 0
  private max_iops:number = 0
  private min_mbps:number = 0
  private max_mbps:number = 0
  private second_confirm =this.oper_type === 'iops' ? '是否确认修改IOPS?': '是否确认修改吞吐量?'
  private data = {
    update_data: 0,
    set:'1',
    reason:''
  }
   created() {
    this.GetLimit()
  }
  get prod_def(){
    return this.oper_type === 'iops' ? `单盘IOPS=min{${this.min_iops}+30*容量，${this.max_iops}}` : `单块云盘吞吐量=min{${this.min_mbps},0.2*容量，${this.max_mbps}}`
  }
  private async GetLimit(){
    let res:any =await Service.get_iops_mbps_limit()
      if (res.code === 'Success') {
        this.min_iops=res.data.CLOUD_DISK_MIN_IPOS
        this.max_iops=res.data.CLOUD_DISK_MAX_IPOS
        this.min_mbps=res.data.CLOUD_DISK_MIN_THROUGE_PUT
        this.max_mbps=res.data.CLOUD_DISK_MAX_THROUGE_PUT
        //获取到数据后重新设置表单的初始值
        this.$nextTick(() => {
          this.data.update_data = this.oper_type === 'iops' ? this.min_iops : this.min_mbps;
        });
      }
  }
  private FnEmit(){
    const form = this.$refs.resetForm as Form
    form.validate( valid=> {
      if (valid) {
        this.second_visible = true
      }
    })
  }
  private async confirm(){
    const form = this.$refs.confirmForm as Form
    form.validate(async (valid)=> {
      if (valid) {
        let req = {
          disk_ids: this.mount_id.map(item => item.disk_id),
          customer_id: this.mount_id[0].customer_id,
          change_reason:this.data.reason
        }
        if(this.oper_type === 'iops'){
          req['disk_iops'] = this.data.update_data
          req['iops_is_default'] = this.data.set
        } else {
          req['band_mbps'] = this.data.update_data
          req['mbps_is_default'] = this.data.set
        }
        let res: any = await Service.change_iops_mbps(req)
        if (res.code === 'Success') {
          this.$message.success(res.message)
          this.back("1")
        } else {
          this.back("0")
        }
      }
    })
  }
  private cancel(){
    this.back("0")
  }
  @Emit("close")
  private back(val){
  }

}
